import Swiper from "swiper";
import { Navigation } from "swiper/modules";

function sliderTwo() {
  new Swiper('.swiper-slider-two .swiper', {
    modules: [Navigation],
    slidesPerView: 1,
    grabCursor: true,
    speed: 1000,
    spaceBetween: 10,
    breakpoints: {
      1180: {
        slidesPerView: 2,
        spaceBetween: 30,
      },
    },
    navigation: {
      prevEl: '.swiper-slider-two .swiper-button-prev',
      nextEl: '.swiper-slider-two .swiper-button-next',
    }
  });
}
sliderTwo();