import Swiper from "swiper";
import { Navigation } from "swiper/modules";

function sliderTwo() {
  new Swiper('.services__table .swiper', {
    modules: [Navigation],
    slidesPerView: 1,
    speed: 1000,
    breakpoints: {
      1180: {
        slidesPerView: 4,
        spaceBetween: 40,
        allowTouchMove: false
      },
    },
    navigation: {
      prevEl: '.services__table .swiper-button-prev',
      nextEl: '.services__table .swiper-button-next',
    }
  });
}
sliderTwo();