import Inputmask from "inputmask";
import sendMail from "./sendMail.js"

export default function sendForm() {

  const validateName = (name) => {
    if (name.length >= 2 && name.length < 50) {
      return name.match(
        /^[а-яА-Я]/
      );
    }
  }

  const validateEmail = (email) => {
    return email.match(
      /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
    );
  };

  const validatePhone = (phone) => {
    return phone.match(
      /^(\+7|7|8)?[\s\-]?\(?[489][0-9]{2}\)?[\s\-]?[0-9]{3}[\s\-]?[0-9]{2}[\s\-]?[0-9]{2}$/
    );
  }

  const validatedName = () => {
    const name = $('input[name="name"]').val();

    if (validateName(name)) {
      $('input[name="name"]').removeClass('error');
    } else {
      $('input[name="name"]').addClass('error');
    }
    if (name == 0) {
      $('input[name="name"]').removeClass('error');
    }
    return false;
  }

  const validatedEmail = () => {
    const email = $('input[name="email"]').val();

    if (validateEmail(email)) {
      $('input[name="email"]').removeClass('error');
    } else {
      $('input[name="email"]').addClass('error');
    }
    if (email == 0) {
      $('input[name="email"]').removeClass('error');
    }
    return false;
  }

  const validatedPhone = () => {
    const phone = $('input[name="phone"]').val();

    if (validatePhone(phone) && phone.length > 0) {
      $('input[name="phone"]').removeClass('error');
      if (!$('.popup__file').hasClass('error')) {
        $('.popup__button').removeClass('disabled');
      }
    } else {
      $('input[name="phone"]').addClass('error');
      $('.popup__button').addClass('disabled');
    }
    return false;
  }

  $('input[name="name"]').on('input', validatedName);
  $('input[name="email"]').on('input', validatedEmail);
  $('input[name="phone"]').on('input', validatedPhone);

  let file = $('form input[type="file"]');
  let extension;

  file.on('input', (function() {
    extension = file[0].files[0].name.split('.')[1];
    let name = file[0].files[0].name;
    let size = file[0].files[0].size;
    if (size < 10485760 && ('pdf' == extension || 'doc' == extension || 'docx' == extension || 'odt' == extension)) {
      $('.popup__file span').text(`Файл ${name} добавлен!`);
      $('.popup__file').removeClass('error').addClass('ok');
      if ($('input[name="phone"]').val().length > 0 && !$('input[name="phone"]').hasClass('error')) {
      }
    } else if (size > 10485760) {
      $('.popup__file span').text('Слишком большой файл!');
      $('.popup__file').removeClass('ok').addClass('error');
      errorMessage('Слишком большой файл!');
    } else {
      $('.popup__file span').text('Неверный формат файла!');
      $('.popup__file').removeClass('ok').addClass('error');
    }
  }));

  function sendPopupForm() {
    let form = $('form');
    let timer = 0;
    if (form) {
      form.on('submit', function(e) {
        e.preventDefault();
        if (
          !$('input[name="name"]').hasClass('error') &&
          !$('input[name="email"]').hasClass('error') &&
          !$('input[name="phone"]').hasClass('error')) {
          sendMail(form).then(function() {
            form.get(0).reset();
            $('.popup__file span').text('Прикрепить файл');
            $('.popup__file').removeClass('ok').removeClass('error');
            $('input').removeClass('error');
            $('.popup__button').addClass('disabled');
            button.text('Отправлено!')
            timer = setTimeout(function() {
              button.text('Оставить отзыв')
              clearTimeout(timer);
            }, 2000);
          });
        }
      });
    }
  }
  sendPopupForm();

  function inputMask() {
    let input =  $('input[type="tel"]');
    Array.from(input).forEach(function(element) {
      let mask = new Inputmask('+7 (999) 999-99-99');
      mask.mask(element);
    });
  }
  inputMask();
}
setTimeout(function() {
  sendForm();
}, 100);