import Swiper from "swiper";
import { Navigation, Thumbs } from "swiper/modules";

function sliderHistory() {
  let swiperSm = new Swiper('.swiper-history-sm', {
    modules: [Navigation],
    slidesPerView: 3,
    allowTouchMove: false,
    initialSlide: 1,
    centeredSlides: true,
    speed: 1000,
    navigation: {
      prevEl: '.history .swiper-button-prev',
      nextEl: '.history .swiper-button-next',
    },
  });
  new Swiper('.swiper-history-lg', {
    modules: [Navigation, Thumbs],
    slidesPerView: 1,
    initialSlide: 1,
    allowTouchMove: false,
    centeredSlides: true,
    freeMode: true,
    watchSlidesProgress: true,
    speed: 1000,
    thumbs: {
      swiper: swiperSm,
    },
    navigation: {
      prevEl: '.history .swiper-button-prev',
      nextEl: '.history .swiper-button-next',
    },
  });
}
sliderHistory();